import React, { Component } from 'react'
import ReactJson from 'react-json-view'

// import css from './HomePage.module.css'

import '../Table/Table.css'

const errSort = (a, b) => b.get("twsTimeMs") - a.get("twsTimeMs")

export default class HomePage extends Component {
  renderCnt = 0

  render() {
    const { props } = this
    const { keys } = props
    const errors = keys.get("0:TwsErrorsLog::")
    this.renderCnt++
    return (
    <div>
      {this.props.devMode?<b>RenderCnt: {this.renderCnt}</b>:null}
      <p/>
      <h3>Runs ({this.props.runsCnt})</h3>
      <p/>
      {/* <ReactJson style={{textAlign:"left"}} name={false} collapsed={2} src={this.props.runs.toJS()} />] */}
      <table style={{maxWidth:"100%"}} className="minimalistBlack">
        <thead>
          <tr>
            <th>RunID</th>
            <th>Time</th>
            <th>Replay</th>
            <th>Status</th>
          </tr>
        </thead>
        <tbody>
        {this.props.runs.map((value, key)=><tr key={key}>
          <td>{value.get("runId")}</td>
          <td>{value.get("time")}</td>
          <td>{value.getIn(["options", "replayMode"])}</td>
          <td>{value.get("status")}</td>
        </tr>).toSetSeq()}
        </tbody>
      </table>
      <p/>
      <h3>Errors ({errors?errors.size:""})</h3>
      {/* <ReactJson  style={{textAlign:"left"}} name={false} collapsed={2} src={errors?errors.sort(errSort).toJS():{}} /> */}
      <table style={{maxWidth:"100%"}} className="minimalistBlack">
        <thead>
          <tr>
          <th>RunId</th>
          <th>Time</th>
          <th>Type</th>
          <th>Subscription</th>
          <th>Error</th>
          </tr>
        </thead>
        <tbody>
        {errors?
        errors.sort(errSort).map((e, key) => {
          const sub = e.get("subscription")
          return <tr key={key}>
            <td>{e.get("runId")}</td>
            <td>{e.get("twsTime")}</td>
            <td>{e.get("type")}</td>
            <td>{sub && sub.getIn(["contract", "name"])}{sub && ":"+sub.getIn(["item"])+""}{sub && " ("+sub.getIn(["contract", "secType"])+"-"+sub.getIn(["contract", "exchange"])+"-"+sub.getIn(["contract", "currency"])+")"}</td>
            <td style={{maxWidth: 600}}>{e.get("errorMsg").substr(0, 200).replace(/\//g, " / ")}</td>
          </tr>
        }).toSetSeq()
        :null}
        </tbody>
      </table>
    </div>
    )
  }
}
