import { connect } from 'react-redux'

import { engine, getState, dispatch } from '../../engine'
import { createClient } from 'xbroker-client'
import { fromJS } from 'immutable-sorted'

const e = engine.subEngine('connections')
const af = e.actionFactory();

const se = engine.subEngine('settings')

export const brokers = {};

const handleError = (error) => {
  if(error instanceof Error) {
    error = {tag: null, status: "error", errorMsg: e.message};
  }
  dispatch(af.value('error', error));
}

const decrTs = (ts) => {
  if(ts === "+") {
    return ts
  }

  const sep = ts.indexOf("-")
  let ts1 = parseInt(ts.substring(0, sep), 10)
  let ts2 = parseInt(ts.substring(sep+1))
  if(ts2 === 0) {
    ts1--
    return ts1.toString()
  } 
  
  ts2--
  return ts1.toString()+"-"+ts2.toString()
}

const readRange = (broker, streamName, limit, reverse, actionCreator, callback) => {
  const cmd = reverse ? "xrevrange" : "xrange"
  const beginSymbol = reverse ? "+" : "-"
  const endSymbol = reverse ? "-" : "+"
  const fieldName = "message"
  const count = 1000
  const list = []

  const cb = (error, data) => {
    if(error) {
      handleError(error)
      if(callback) {
        callback(error)
      }
      return
    }
    if(data.status !== "ok") {
      handleError(data)
      if(callback) {
        callback(error)
      }
      return
    }

    try {
      const xbatch = [];
      let lastTs = beginSymbol
      for(let i = 0; i < data.result.length; i++) {
        const result = data.result[i]
        lastTs=result[0]
        const fieldRecord = result[1]
        if(fieldRecord[0] !== fieldName) {
          handleError("Unexpected field name: "+fieldRecord[0])
          if(callback) {
            callback(error)
          }
          return  
        }
        const record = fieldRecord[1]
        const rec = JSON.parse(record)
        list.push(rec)
        xbatch.push(actionCreator(rec));
        limit--
      }
      if(limit > 0 && data.result.length >= count) {
        lastTs = decrTs(lastTs)
        const cnt = limit < count ? limit : count
        broker.command("redis", cmd, [streamName, lastTs, endSymbol, "count", cnt], cb);
      } else {
        if(callback) {
          callback(null, list)
        }
      }
      if(xbatch.length > 0) {
        dispatch(af.batch(xbatch));
      }
    } catch(error) {
      handleError(error)
    }
  }

  const cnt = limit < count ? limit : count
  broker.command("redis", cmd, [streamName, beginSymbol, endSymbol, "count", cnt], cb);
}

const readRangeList = async (broker, streamName, limit, reverse, actionCreator, callback) => {
  const cmd = reverse ? "xrevrange" : "xrange"
  const beginSymbol = reverse ? "+" : "-"
  const endSymbol = reverse ? "-" : "+"
  const fieldName = "message"
  const count = 1000
  const list = []

  const cb = (error, data) => {
    if(error) {
      handleError(error)
      if(callback) {
        callback(error)
      }
      return
    }
    if(data.status !== "ok") {
      handleError(data)
      if(callback) {
        callback(error)
      }
      return
    }

    try {
      let lastTs = beginSymbol
      for(let i = 0; i < data.result.length; i++) {
        const result = data.result[i]
        lastTs=result[0]
        const fieldRecord = result[1]
        if(fieldRecord[0] !== fieldName) {
          handleError("Unexpected field name: "+fieldRecord[0])
          if(cb) {
            cb(error)
          }
          return  
        }
        const record = fieldRecord[1]
        const rec = JSON.parse(record)
        list.push(rec);
        limit--
      }

      if(limit > 0 && data.result.length >= count) {
        lastTs = decrTs(lastTs)
        const cnt = limit < count ? limit : count
        broker.command("redis", cmd, [streamName, lastTs, endSymbol, "count", cnt], cb);
      } else {
        if(reverse) {
          list.reverse()
        }
        dispatch(actionCreator(list));
        if(callback) {
          callback(null, list)
        }
      }
    } catch(error) {
      handleError(error)
    }
  }

  const cnt = limit < count ? limit : count
  broker.command("redis", cmd, [streamName, beginSymbol, endSymbol, "count", cnt], cb);
}

const subscribe = async (broker, limit) => {
  const batch = []

  const listener = {
    message: (data) => {
      const message = fromJS(JSON.parse(data["message"]))
      const action = af.insert('messages', data.channel, message)
      batch.push(action)
      const keys = e.get(getState(), 'keys')
      let bars = keys.get(data.channel)
      if(bars) {
        if(bars.size === 0) {
          bars = bars.push(message)
        } else {
          const lastBar = bars.last()
          if(lastBar.get("timeMs") < message.get("timeMs")) {
            bars = bars.push(message)
          } else if(lastBar.get("timeMs") === message.get("timeMs")) {
            bars = bars.set(-1, message)
          }
        }
        const action2 = af.insert('keys', data.channel, bars);
        batch.push(action2);
      }
    },

    flush: () => {
      dispatch(af.batch(batch))
      batch.length = 0
    },
  };

  try {
    await broker.commandAsync("redis", "psubscribe", ["*"], listener);
  } catch(error) {
    handleError(error)
  }
}

const readRuns = (broker, limit) => {
  readRange(broker, "ChaserRuns", limit, true,
    rec => af.insert("runs", rec.runId, rec),
    (err, data) => {
      if(!err) {
        readRange(broker, "ChaserLogs", limit, false,
          rec => {
            return af.batch([
              af.insert("runsLog", rec.runId, rec),
              af.update("runs", rec.runId, {$set: {status: rec.status}})
            ])
          },
          (err, data) => {
            if(!err) {

            }
          })
      }
    })
}

const readKeys = async (broker, limit) => {
  try {
    const keysData = await broker.commandAsync("redis", "keys", ["*"]);
    const keys = keysData.result;

    for(let i in keys) {
      const key = keys[i]
      if(key === "ChaserRunId") {
        continue
      }
      readRangeList(broker, key, limit, true, list => af.insert("keys", key, list))
    }
  } catch(error) {
    handleError(error)
  }
}

const load = (broker) => {
  const limit = se.get(getState(), "dataBars")

  dispatch(af.reset("runs"));
  dispatch(af.reset("runsLog"));
  dispatch(af.reset("keys"));
  dispatch(af.reset("messages"));

  readRuns(broker, 100)
  readKeys(broker, limit)
  subscribe(broker, limit)

}

const OnConnectionOpen = async (id, broker) => {

  if(!broker) {
    return
  }

  load(broker)
}

const OnConnectionClose = (id) => {}

const updateBrokers = async () => {
  const conns = e.get(getState(), 'connections');
  conns.forEach((conn) => {
    const id = conn.get("id");
    if(!brokers[id]) {
      const props = {
        maxSentCommandsCnt: 16,
        timeoutMs: 60000,

        changeState: (state) => {
          if(!brokers[id]) {
            return;
          }
          const action = af.update('connections', id, {$set: {state}})
          dispatch(action)
          if(state === 'open') {
            OnConnectionOpen(id, brokers[id])
          }
        },
        setMessage: (message) => {
          if(!brokers[id]) {
            return;
          }
          const action = af.update('connections', id, {$set: {message}})
          dispatch(action)
        },
      };
      const url = conn.get("url");
      brokers[id] = createClient(url, props);
    }
  })
};

updateBrokers();

const mapStateToProps = (state, ownProps) => ({
  connections: e.get(state, 'connections'),
  connectionsCnt: e.get(state, 'connectionsCnt'),
})

const mapRepoToProps = (repo, ownProps) => mapStateToProps(e.head(repo), ownProps)

const mapDispatchToProps = (dispatch, ownProps) => ({
  addConnection: (connection) => {
    const connectionId = e.get(getState(), 'connectionId')
    const id = "conn"+connectionId;
    connection = connection.set("id", id);
    const action1 = af.value('connectionId', (connectionId+1))
    const action2 = af.insert('connections', id, connection)
    const action = af.batch([action1, action2])
    dispatch(action)
    updateBrokers();
  },
  removeConnection: (id) => {
    const broker = brokers[id]
    if(broker) {
      delete brokers[id]
      broker.close()
      OnConnectionClose(id)
    }
    const action = af.remove('connections', id)
    dispatch(action)
  },
})

const ConnectionsContainer = connect(
  mapRepoToProps,
  mapDispatchToProps
)

export default ConnectionsContainer
