import React, { Component } from 'react'

import Chart from '../components/Chart'

import throttle from 'react-throttle-render'

import css from './DashPage.module.css'

// import '../Table/Table.css'

class DashPage extends Component {
  renderCnt = 0
  cache = {}

  render() {
    this.renderCnt++
    const time = this.props.messages.get("0:TwsTime::")
    // const time = this.props.messages.get("20:TwsTime::")
    return (
    <div>
      {this.props.devMode?<b>RenderCnt: {this.renderCnt}</b>:null}
      <p/>
      Keys Count: {this.props.keysCnt}
      <p/>
      TWS Time: {time && time.get("twsTime")}
      <p/>
      {this.props.keys
        .filter((data, k)=>k.startsWith("0:") && !k.endsWith("::") && data.size > 1)
        // .filter((data, k)=>k.startsWith("20:") && data.size > 1)
        .sortBy((v, k)=>{
          const ka = k.split(":")
          return ka[2]+":"+ka[3]+":"+ka[1]+":"+ka[0]
        })
        .slice(0, 1000)
        .map((data, key)=>{
          let ch = this.cache[key]
          if(!ch) {
            ch = {data: null, updateCnt: 0}
            this.cache[key] = ch
          }
          if(ch.data === data) {
            return ch.obj
          }

          ch.updateCnt++
          ch.data = data
          // const value = data.last()
          const jsData = data.slice(data.size>5?data.size-5:0, data.size).toJS().map(v => ({
            date: new Date(v.timeMs),
            open: v.open,
            high: v.high,
            low: v.low,
            close: v.close,
            volume: v.volume,
          }))
          const obj = <div key={key} className={css.chart}>
            <p>{key} ({ch.updateCnt})</p>
            {/* <p>{value.get("close").toString().padStart(10, " ").substring(0, 10)}</p> */}
            {/* <p>{value.get("lastUpdateTime") || value.get("twsTime")}</p> */}
            <div><Chart width={380} ratio={1} data={jsData}/></div>
          </div>
          ch.obj = obj
          return obj
      }).toSetSeq()}
    </div>
    )
  }
}

const ThrottledComponent = throttle(250)(DashPage)

export default ThrottledComponent
